        <h1 id="a41">什么是ascii码表？</h1>
        <p>美国信息交换标准代码</p>
        <p>
          每个ASCII码存储在一个Byte中，从0到127的数字代表不同的常用符号，如65代表大写A，97代表小写a。由于ASCII字节的7位中，最高位没有使用，那么最高位也被纳入这个内部代码，成为8位ASCII(ExtendedASCII代码。这种内部代码现在常用于许多特殊符号，如外语和表格。
