<script>
import { compted, jsData, cssData, vueData } from "@/assets/app"
export default {
  data() {
    return {
      isCollapse: true,

      // 计算列表数据展示
      compted,
      jsData,
      cssData,
      vueData,
      restaurants: [],
      state1: "",
      timeout: null,
    }
  },
  mounted() {
    this.restaurants = this.loadAll()

    console.log([...this.cssData])
  },
  methods: {
    toTop() {
      document.documentElement.scrollTop = document.body.scrollTo = 0
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },

    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    add(href) {
      this.isCollapse = true
      location.hash = href
    },
    handleSelect(item) {
      console.log(item)
      location.href = item.href
    },
    querySearch(queryString, cb) {
      var restaurants = this.restaurants
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants
      // 调用 callback 返回建议列表的数据
      cb(results)
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        )
      }
    },

    loadAll() {
      let arr = []
      this.cssData.forEach((e) => {
        arr.push({
          value: e.data,
          href: e.href,
        })
      })

      this.jsData.forEach((e) => {
        arr.push({
          value: e.data,
          href: e.href,
        })
      })
      this.vueData.forEach((e) => {
        arr.push({
          value: e.data,
          href: e.href,
        })
      })
      this.compted.forEach((e) => {
        arr.push({
          value: e.data,
          href: e.href,
        })
      })

      return arr
    },
  },
}
</script>

